<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Login</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
        <ion-input placeholder="Enter username" @ionChange="validate" v-model="username"></ion-input>
        <ion-input placeholder="Enter password" @ionChange="validate" v-model="password" type="password"></ion-input>
        <ion-button :disabled="disabled" expand="full" @click="login">Login</ion-button>
      </ion-content>
  </ion-page>
</template>
<script>
import { IonContent, IonInput, IonButton, toastController, IonPage, IonToolbar, IonHeader, IonTitle } from '@ionic/vue';
import { warning } from 'ionicons/icons';
import { defineComponent } from 'vue';
import { mapGetters, mapActions } from 'vuex';
 import { useRouter } from 'vue-router';
import axios from 'axios';

export default defineComponent({
  components: { IonInput, IonButton, IonContent, IonPage, IonToolbar, IonHeader, IonTitle },
  setup() {
    const router = useRouter();
    return { warning, router };
  },
  methods: {
    ...mapGetters('login',
      ['jwt']
    ),
    ...mapActions('login',{
      doLogin:'doTeacherLogin'
    }),
     async failedLoginToast() {
        const toast = await toastController
          .create({
            message: 'Your login data was incorrent. Try again.',
            duration: 2000
          })
        return toast.present();
      },
      validate(){
        if (this.username != "" && this.password != ""){
          this.disabled = false;
        }else{
          this.disabled = true;
        }
      },
      async login(){
        const payload = {
            username: this.username,
            password: this.password
        };

        axios.post(process.env.VUE_APP_TEACHER_API_URL+'/teachermanagement/teachers/login', payload).then((response) => {
          this.doLogin(response.data);
          this.username = "";
          this.password = "";
          this.disabled = true;
          this.eventBus.emit('login', true);
          this.router.push("/");
        }).catch((error) => {
          console.error(error)
          this.failedLoginToast();
        });
      }
  },
  data: () => ({
    username: "",
    password: "",
    disabled: true
  }),
});
</script>

